@import url('https://fonts.googleapis.com/css?family=Roboto:400,700');
@import './perfect-scrollbar.css';
@import "../node_modules/react-table/react-table.css";
@import "../node_modules/react-dropdown-tree-select/dist/styles.css";

@font-face {
  font-family: 'react-categories-icon';
  src:
    url('../assets/react-categories-icon.ttf?aqiic1') format('truetype'),
    url('../assets/react-categories-icon.woff?aqiic1') format('woff'),
    url('../assets/react-categories-icon.svg?aqiic1#react-categories-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="react-categories-icon-"], [class*=" react-categories-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'react-categories-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'react-image-editor';
  src:
    url('../assets/react-image-editor.ttf?bikz36') format('truetype'),
    url('../assets/react-image-editor.woff?bikz36') format('woff'),
    url('../assets/react-image-editor.svg?bikz36#react-image-editor') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="react-icons-icon-moon-"], [class*=" react-icons-icon-moon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'react-image-editor' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$alias: "AA_PB-";
$menu_background: #fff;

body.toplevel_page_image_editor {
  #wpbody-content {
    padding-bottom: 0;
  }

  #wpfooter {
    display: none;
  }
}


.__tmp-image {
  display: none;
}

// list page
$alias: 'AA_PB-List-';
.#{$alias}wrapper {
  font-family: 'Roboto';
  margin-left: -20px;
  padding: 0px;
  padding-left: 0px;
  height: calc(100vh - 32px);
  position: relative;
  z-index: 9999999;
  display: grid;
  grid-template-columns: 360px 1fr;

  .#{$alias}logo {
    display: grid !important;
    grid-template-columns: 100px 1fr;
    text-decoration: none;

    div {
      span {
        display: block;
        color: #fff;

        &:first-child {
          font-size: 18px;
        }
        &:last-child {
          font-size: 12px;
          margin-top: 10px;
        }
      }
    }
  }

  .#{$alias}no-results {
    background: #fff;
    padding: 10px 20px;
    color: #41b7a7;
    border-radius: 20px;
    font-weight: bold;
  }

  .#{$alias}launch-your-search {
    font-weight: bold;
    background: #fff;
    padding: 10px 20px;
    display: block;
    color: #41b7a7;
    border-radius: 20px;
    font-size: 14px;
  }

  .#{$alias}btn {
    font-size: 14px;
    color: #fff;
    background: #41b7a7;
    padding: 5px 14px;
    font-size: 12px;
    text-decoration: none;
    border-radius: 15px;
    margin-top: 12px;
    text-transform: uppercase;
    font-weight: bold;

    &.#{$alias}gray {
      background: #f2f2f2;
      color: #757575;
    }

    &.#{$alias}small {
      font-size: 10px;
      padding: 2px 8px;
    }

    &:hover {
      background: #e33d55;
      color: #fff;
    }
  }

  .#{$alias}content {
    background: #f3f4f6;
    padding: 20px 20px 20px 20px;

    h1 {
      padding: 0;
      margin: 0;
      font-size: 18px;
      margin-bottom: 30px;
      margin-top: 10px;
      span {
        font-size: 14px;
      }
    }

    .#{$alias}pagination > li {
      display: inline-block;
      padding-left: 0;
      list-style: none;
      > {
        a, span {
          position: relative;
          float: left;
          padding: 6px 12px;
          text-decoration: none;
          color: #2c689c;
          background-color: #fff;
          border: 1px solid #ddd;
          margin-left: -1px;
        }
      }
      &.active > a {
        color: #fff;
        background-color: #2e3345;
        border-color: #1e7e34;
      }
      > a:hover {
        background-color: #2e3345;
        color: white;
        cursor: pointer;
      }
      &:first-child > {
        a, span {
          margin-left: 0;
          padding: 0px;
          border-bottom-left-radius: 4px;
          border-top-left-radius: 4px;
          display: none !important;
        }
      }
      &:last-child > {
        a, span {
          border-bottom-right-radius: 4px;
          margin-right: 0;
          padding: 0px !important;
          border-top-right-radius: 4px;
          display: none !important;
        }
      }
    }

    .#{$alias}image-view {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 999;
      background: #fff;
      padding: 20px;
      user-select: none;

      h1 {
        position: absolute;
        top: 40px;
        left: 40px;
        display: block;
        position: absolute;
        z-index: 99;
        margin: 0;
        padding: 0;
        background: #fff;
        font-size: 12px;
        text-transform: uppercase;
        padding: 5px 25px;
        line-height: 20px;
      }
      .#{$alias}resolution {
        position: absolute;
        bottom: 20px;
        right: 20px;
        display: inline-block;
        background: rgba( #000, 0.5 );
        padding: 2px 15px;
        font-size: 11px;
        color: rgba( #fff, 0.6 );
        z-index: 99;
      }

      .#{$alias}zoom {
        position: absolute;
        bottom: 40px;
        left: 40px;
        z-index: 99;
        width: 40px;
        height: 80px;

        > div {
          position: absolute;
          bottom: 100%;
          background: #212121;
          margin-bottom: 5px;
          width: 35px;
          font-size: 8px;
          text-align: center;
          color: #fff;
        }

        .dashicons {
          border: 1px solid #212121;
          font-size: 24px;
          width: auto;
          height: auto;
          padding: 5px;
          background: rgba( #E0E0E0, 0.5 );
          margin-bottom: 4px;
          line-height: 24px;
          cursor: pointer;

          &:first-child:before {
            position: relative;
            top: 2px;
          }
          &:hover {
            background: #fff;
          }
        }
      }
      .#{$alias}image-view-wrapper {
        height: 100%;

        img {
          transform-origin: top left;
        }
      }

      .dashicons-no-alt {
        position: absolute;
        top: 40px;
        right: 40px;
        z-index: 999;
        background: #e53935;
        color: #fff;
        padding: 5px;
        cursor: pointer;
        border: 1px solid rgba( #fff, 0.2 );

        &:hover {
          background: #f44336;
          border-color: #fff;
        }
      }
    }

    .#{$alias}search-results-list {
      margin: 0;
      padding: 0;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 25px;

      > li {
        border: 1px solid #E0E0E0;
        background: #EEEEEE;
        text-align: center;
        padding: 10px;

        .#{$alias}thumb {
          position: relative;
          height: 220px;

          img {
            display: block;
          }

          .#{$alias}resolution {
            position: absolute;
            top: 5px;
            right: 5px;
            display: inline-block;
            background: rgba( #000, 0.5 );
            padding: 2px 15px;
            font-size: 11px;
            color: rgba( #fff, 0.6 );
          }

          .#{$alias}view-image {
            position: absolute;
            bottom: 85px;
            display: inline-block;
            background: #ac658f;
            color: #fff;
            text-decoration: none;
            padding: 6px 25px;
            transform: translateX(-50%);
            border-radius: 15px;
            cursor: pointer;
            font-weight: bold;
            text-transform: uppercase;
            font-size: 12px;

            &:hover {
              background: #549fc0;
            }
          }

          .#{$alias}imported {
            position: absolute;
            bottom: 45px;
            display: inline-block;
            background: #ed553b;
            color: #fff;
            text-decoration: none;
            padding: 4px 25px;
            transform: translateX(-50%);
            border-radius: 15px;
            font-weight: bold;
            text-transform: uppercase;
            font-size: 12px;

            &:hover {
              background: #e33d55;
            }
          }
          .#{$alias}already-in-list {
            position: absolute;
            bottom: 45px;
            width: 120px;
            display: inline-block;
            background: #ed553b;
            color: #fff;
            padding: 3px 15px;
            border-radius: 15px;
            text-transform: uppercase;
            font-size: 12px;
            font-weight: bold;
            transform: translateX(-50%);
          }

          .#{$alias}get-image {
            position: absolute;
            bottom: 45px;
            width: 120px;
            transform: translateX(-50%);
          }
        }

        .#{$alias}meta  {
          background: #2e3345;
          display: grid;
          grid-template-columns: 1fr 1fr 1fr 1fr;
          grid-gap: 10px;
          color: #fff;
          padding: 10px;
          overflow: hidden;

          > div {
            display: inline-block;
            line-height: 22px;

            span {
              font-size: 20px;
              margin-right: 4px;
              display: block;
              width: 100%;
              text-align: center;
            }
          }

          .#{$alias}colors {
            display: inline-block;
            font-size: 0;
            padding: 0;
            margin: 0;
            text-align: right;
            height: 20px;

            li {
              display: inline-block;
              width: 20px;
              height: 20px;
              cursor: pointer;
              border: 1px solid rgba( #E0E0E0, 0.5 );
              margin-right: 2px;

              &:hover {
                opacity: 0.6;
                border: 1px solid #fff;
              }
            }
          }
        }
      }
    }
  }

  .#{$alias}nav {
    background: $menu_background;
    position: relative;

    .#{$alias}search-colors {
      display: block;

      ul {
        margin: 0;
        padding: 0;

        &:after {
          content: '';
          display: block;
          clear: both;
        }

        li {
          margin: 0;
          padding: 0;
          width: 24px;
          height: 24px;
          border-radius: 50%;
          display: inline-block;
          float: left;
          margin-right: 8px;
          margin-bottom: 8px;
          text-align: center;
          line-height: 25px;
          color: transparent;
          position: relative;
          cursor: pointer;
           box-shadow: 2px 2px 2px #aaaaaa;

          &.__is_color_selected:after {
            color: #212121;
            font-family: dashicons;
            content: "\f147";
            text-shadow: 0 0 1px #BDBDBD;
            font-size: 14px;
            position: absolute;
            top: 0;
            left: 5px;
          }

          &.#{$alias}no-color {
            span {
              position: absolute;
              top: 0;
              left: 10px;
              content: "";
              display: block;
              width: 4px;
              bottom: 0;
              background: #e53935;
              transform: rotate(-45deg);
            }
          }
        }
      }
    }

    .#{$alias}search-res {
      margin-bottom: 60px;
      table {
        width: 100%;
        border: 1px solid #EEEEEE;

        thead {
          th {
            font-size: 13px;
            padding-bottom: 5px;
          }
        }

        tbody {
          label {
            border: 1px solid #EEEEEE;
            background: #F5F5F5;
            padding: 5px;
            text-align: center;
            font-size: 11px;
            line-height: 12px;

            &:hover,
            &.__is_selected {
              background: #2e3345;
              color: #fff;
            }
          }
        }
      }
    }

    .#{$alias}search-btn {
      border-radius: 15px;
      text-align: center;
      padding-top: 12px;
      padding-bottom: 12px;
      display: block;
      width: 100%;
      background: #41b7a7;
      border: 1px solid transparent;

      &:focus {
        outline: none;
      }
      &:hover {
        background: #654574;
        cursor: pointer;
      }
    }

    .#{$alias}search-editors_choice {
      margin-top: 20px;
      border: 1px solid #E0E0E0;
      padding: 15px;
      background: #F5F5F5;

      div.components-base-control__field {
        display: grid !important;
        grid-template-columns: 110px 1fr;
      }
    }

    .#{$alias}search-select-box {
      display: grid !important;
      grid-template-columns: 130px 1fr;
      border: 1px solid #E0E0E0;
      padding: 15px;
      background: #F5F5F5;
      margin-bottom: 20px;

      h3 {
        margin: 0 !important;
        margin-bottom: 10px !important;
        position: relative;
        top: 7px;
      }
    }

    .#{$alias}search-categories {
      border: 1px solid #E0E0E0;
      padding: 15px;
      background: #F5F5F5;
      margin-bottom: 20px;

      h3 {
        margin: 0 !important;
        margin-bottom: 10px !important;
      }
    }

    .#{$alias}search-categories > div {
      display: grid !important;
      grid-template-columns: 1fr 1fr;

      .components-base-control__field {
        display: grid !important;
        grid-template-columns: 1fr 1fr;
        text-transform: capitalize;

        input {
          margin-left: 10px;
        }
      }
    }

    .#{$alias}search-query {
      display: block;
      margin-bottom: 20px;

      input {
        border: 1px solid #f0f0f0;
        width: 100%;
        padding: 12px 15px;
        box-shadow: none;
      }

      a {
        display: inline-block;
      }

      ul {
        margin-left: 5px;
        border: 1px solid #f2f2f2;
        background: #f2f2f2;
        padding: 10px;

        li {
          margin-bottom: 0px;
          font-size: 11px;
        }
      }
    }

    .#{$alias}search-box {
      padding: 20px;

      label {
        display: block;
      }

      h3 {
        margin-top: 25px;
        font-size: 13px;
        margin-bottom: 10px;
      }
    }
    a.#{$alias}logo {
      display: block;
      height: 100px;
      width: 100%;
      background: #2e3345;
      display: flex;
      align-items: center;
      justify-content: center;


      img {
        display: block;
        margin: 20px;
      }
    }
  }

  .#{$alias}overlay-loading {
    position: fixed;
    top: 0;
    left: 160px;
    bottom: 0;
    right: 0;
    z-index: 999999;
    background: rgba( 0,0,0, 0.8 );

    > div {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 200px;
      text-align: center;
      margin-left: -100px;
      margin-top: -50px;

      h2 {
        color: rgba( #fff, 0.7 );
        text-transform: uppercase;
      }
    }
  }
}

.#{$alias}in-media-wrapper {

  .#{$alias}wrapper {
    margin-left: 0px;
    padding: 0px;
    padding-left: 0px;

    .#{$alias}content {
      position: relative;
    }
    .#{$alias}overlay-loading {
      position: absolute;
      left: -360px;
      bottom: 0;
    }
  }
}



